<template>
  <div>
    <base-header class="pb-6" type="secondary">
      <div class="row align-items-center py-4">
        <div class="col-lg-6 col-7">
          <h6 class="h2 d-inline-block mb-0">Dashboard</h6>
        </div>
      </div>
      <!--      Notifications-->
      <transition name="fade">
        <notification-area v-if="showDashboardSwitch" />
      </transition>
      <!--      Stats-->
      <transition name="fade">
        <Stats />
      </transition>
    </base-header>

    <div class="container-fluid mt--6">
      <!--Widgets-->
      <div id="mainCourseArea">
        <div v-show="!mainCourse.lessonData" class="display-2 pt-6 pb-5">
          Retrieving Courses...
        </div>
        <transition name="fade">
          <div v-if="mainCourse.lessonData">
            <main-area
              :lessonData="mainCourse.lessonData"
              :title="mainCourse.course.course_title"
              :courseId="mainCourse.course.id"
              @dropthis="dropThisCourse(mainCourse.course.id, 'main')"
            />
            <div v-if="mainCourse.lessonData.length > 5">
              <lesson-deck
                :lessonData="mainCourse.lessonData.slice(5, 50)"
                :courseId="mainCourse.course.id"
                :viewMoreUrl="`/courses/${mainCourse.course.id}`"
                @dropthis="dropThisCourse(mainCourse.course.id, 'main')"
              />
            </div>
          </div>
        </transition>
      </div>
      <transition name="fade">
        <free-upgrade v-if="needsUpgrade"></free-upgrade>
      </transition>
      <div v-if="courseData.length > 0">
        <transition-group name="list" tag="div">
          <lesson-deck
            v-for="course in courseData.slice(0, 10)"
            v-show="course.lessonData.length > 0"
            :key="course.course.id"
            :title="course.course.course_title"
            :lessonData="course.lessonData"
            :courseId="course.course.id"
            :viewMoreUrl="`/courses/${course.course.id}`"
            @dropthis="dropThisCourse(course.course.id, 'course')"
          />
        </transition-group>
      </div>
      <div id="userBookmarks" v-show="bookmarkData !== mainCourse.lessonData">
        <div v-if="bookmarkData.length === 0">
          <h2 class="display-3">Saved Lessons</h2>
          <div class="text-center m-5">
            <h5>
              No bookmarked lessons just yet! Mark some lessons for later
              studies, and they'll appear here.
            </h5>
          </div>
        </div>
        <transition name="fade">
          <lesson-deck
            v-show="bookmarkData.length > 0"
            title="Saved Lessons"
            :lessonData="bookmarkData"
            viewMoreUrl="https://chinesepod.com/dashboard/selfstudy"
          />
        </transition>
      </div>
      <div id="moreCourses">
        <transition name="fade">
          <course-deck
            v-show="moreCourses.length > 0"
            title="Explore More Courses"
            :courseData="moreCourses"
            viewMoreUrl="/courses"
            @addedcourse="addCourse"
          />
        </transition>
      </div>
      <div id="latestLessons">
        <div v-if="latestData.length === 0">
          <h2 class="display-3">Latest Lessons</h2>
          <div class="text-center m-5">
            <h5>
              We weren't able to retrieve the latest lessons. Please refresh
              this page later.
            </h5>
          </div>
        </div>
        <transition name="fade">
          <lesson-deck
            v-show="latestData.length > 0"
            title="Latest Lessons"
            :lessonData="latestData"
            viewMoreUrl="https://chinesepod.com/dashboard"
          />
        </transition>
      </div>
      <div id="userHistory">
        <div v-if="historyData.length === 0">
          <h2 class="display-3">Lesson History</h2>
          <div class="text-center m-5">
            <h5>
              You haven't studied any lessons yet! Mark a lesson as completed,
              and it will appear here.
            </h5>
          </div>
        </div>
        <transition name="fade">
          <lesson-deck
            v-show="historyData.length > 0"
            title="Lesson History"
            :lessonData="historyData"
            viewMoreUrl="https://chinesepod.com/dashboard/selfstudy"
          />
        </transition>
      </div>
      <!--End Widgets-->
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";

// Components
import Stats from "@/components/Dashboard/Stats";
import LessonDeck from "@/components/Dashboard/LessonDeck";
import CourseDeck from "@/components/Dashboard/CourseDeck";
import MainArea from "@/components/Dashboard/MainArea";
import FreeUpgrade from "../../components/Dashboard/FreeUpgrade";
import NotificationArea from "../../components/Dashboard/NotificationArea";

export default {
  components: {
    NotificationArea,
    FreeUpgrade,
    LessonDeck,
    CourseDeck,
    MainArea,
    Stats
  },
  title() {
    return `Dashboard | ChinesePod`;
  },
  data() {
    return {
      // courseSync: false,
      // bookmarkSync: false,
      // historySync: false,
      // latestSync: false,
      // moreCoursesSync: false,
    };
  },
  computed: {
    isUserAuthenticated() {
      return this.$store.state.token !== "";
    },
    mainCourse() {
      return this.$store.state.mainCourse;
    },
    courses() {
      return this.$store.state.courses;
    },
    courseData() {
      return this.$store.state.courseData;
    },
    bookmarkData() {
      return this.$store.state.bookmarkData;
    },
    historyData() {
      return this.$store.state.historyData;
    },
    latestData() {
      return this.$store.state.latestData;
    },
    moreCourses() {
      return this.$store.state.moreCourses;
    },
    needsUpgrade() {
      return this.$store.state.userInfo.upgrade.needsUpgrade;
    },
    ...mapState({
      showDashboardSwitch: "showDashboardSwitch"
    })
  },
  methods: {
    async getCourseData(courseId) {
      return await this.axios
        .get(
          process.env.VUE_APP_ROOT_API +
            `/api/v1/dashboard/course-lessons?courseId=${courseId}&limit=20`
        )
        .then(async ({ data }) => {
          return data;
        })
        .catch(() => {});
    },
    async buildMainCourse() {
      let courseCleanup = [];
      return new Promise(async (resolve, reject) => {
        if (this.courses.length === 0 && this.bookmarkData.length > 0) {
          this.$store.commit("SET_MAIN_COURSE", {
            course: {
              course_title: "Your Bookmarked Lessons",
              bookmarks: true
            },
            lessonData: this.bookmarkData
          });
          resolve();
        } else if (this.courses.length > 0) {
          let lessonData = await this.getCourseData(
            this.courses[0]["course"].id
          );
          if (lessonData.length > 0) {
            this.$store.commit("SET_MAIN_COURSE", {
              course: this.courses[0]["course"],
              lessonData: lessonData
            });
            resolve();
          } else if (this.courses.length > 1) {
            this.courseCleanup.push(this.courses[0].course.id);
            lessonData = await this.getCourseData(this.courses[1]["course"].id);
            if (lessonData.length > 0) {
              this.$store.commit("SET_MAIN_COURSE", {
                course: this.courses[1]["course"],
                lessonData: lessonData
              });
              resolve();
            }
          } else if (this.courses.length > 2) {
            courseCleanup.push(this.courses[1].course.id);
            lessonData = await this.getCourseData(this.courses[2]["course"].id);
            if (lessonData.length > 0) {
              this.$store.commit("SET_MAIN_COURSE", {
                course: this.courses[2]["course"],
                lessonData: lessonData
              });
              resolve();
            }
          }
        } else {
          this.$store.commit("SET_MAIN_COURSE", {
            course: { course_title: "All the Way To Intermediate", id: 951 },
            lessonData: await this.getCourseData(951)
          });
          resolve();
        }
        courseCleanup.forEach(async courseId => {
          await this.axios.post(
            process.env.VUE_APP_ROOT_API + "/api/v1/dashboard/toggle-course",
            {
              courseId: courseId,
              status: false
            }
          );
        });
        reject();
      });
    },
    async buildUserCourses() {
      let relevantCourses = this.courses.slice(
        this.courses.findIndex(course => {
          return course.course.id === this.mainCourse.course.id;
        }) + 1
      );
      let newCourses = [];
      relevantCourses.forEach(async ({ course }) => {
        newCourses.push({
          course: course,
          lessonData: await this.getCourseData(course.id)
        });
      });
      if (this.courseData !== newCourses) {
        this.$store.commit("SET_COURSE_DATA", newCourses);
      }
    },
    addCourse: async function(course) {
      await this.axios
        .get(
          process.env.VUE_APP_ROOT_API +
            `/api/v1/dashboard/course-lessons?courseId=${course.id}&limit=20`
        )
        .then(({ data }) => {
          this.$store.commit("ADD_COURSE_DATA", {
            course: course,
            lessonData: data
          });
        })
        .catch(() => {});
    },
    dropThisCourse(courseId, area) {
      if (area === "main") {
        if (this.courseData.length > 0) {
          this.$store.commit("SET_MAIN_COURSE", this.courseData[0]);
          if (this.courseData.length > 1) {
            this.$store.commit("SET_COURSE_DATA", this.courseData.slice(1, 5));
          } else {
            this.$store.commit("SET_COURSE_DATA", []);
          }
        } else if (this.bookmarkData) {
          this.$store.commit("SET_MAIN_COURSE", {
            course: { course_title: "Your Bookmarked Lessons" },
            lessonData: this.bookmarkData
          });
        } else {
          this.$store.commit("SET_MAIN_COURSE", {});
        }
      } else if (area === "course") {
        this.$store.commit(
          "SET_COURSE_DATA",
          this.courseData.filter(course => {
            return course.course.id !== courseId;
          })
        );
      }
    },
    async buildDashboard() {
      this.$store.dispatch("getUserStats");
      this.$store.dispatch("getUserCourses").then(async () => {
        await this.buildMainCourse();
        await this.buildUserCourses();
      });
      this.$store.dispatch("updateBookmarks");
      this.$store.dispatch("getMoreCourses");
      this.$store.dispatch("updateHistory");
      this.$store.dispatch("updateLatestLessons");
    }
  },
  async created() {
    if (!this.$store.state.token) {
      await this.$store.dispatch("getToken").catch(() => {
        this.$store.commit("RESET_USER_INFO");
        window.location.href = "/login";
      });
    }
    await this.$store.dispatch("getUserInfo").catch(() => {
      this.$store.commit("RESET_USER_INFO");
      window.location.href = "/login";
    });
    await this.buildDashboard();
  },
  mounted() {
    this.$store.commit("TOGGLE_BANNER", true);
  }
};
</script>
<style lang="scss">
.fade-enter-active,
.fade-leave-active {
  transition: opacity 1.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.fade-leave-active {
  transition: all 0s;
}
.list-item {
  display: inline-block;
  margin-right: 10px;
}
.list-enter-active {
  transition: all 1s;
}
.list-enter/* .list-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translatex(1600px);
}
.list-leave-to {
  opacity: 0;
}
</style>
