<template>
  <div>
    <div v-if="title" class="row mb-1">
      <div class="col align-middle">
        <base-button
          v-if="courseId"
          size="sm"
          type="danger"
          class="float-right"
          @click.prevent="dropCourse"
          ><i class="fas fa-times"></i>Remove</base-button
        >
        <h2 class="display-4">{{ title }}</h2>
      </div>
    </div>
    <swiper :options="swiperOption" ref="mySwiper">
      <swiper-slide v-for="lesson in lessonData" :key="lesson.id">
        <lesson-card :lesson="lesson" :courseId="courseId" />
      </swiper-slide>
      <swiper-slide>
        <a :href="viewMoreUrl">
          <card type="frame">
            <div class="d-flex align-items-center">
              <div class="col text-center py-4 py-lg-2">
                <img
                  src="@/assets/img/brand/symbol.svg"
                  class="img-fluid cpod-symbol"
                  alt="ChinesePod"
                />
                <div class="text-center mt-2 mb-0">
                  <h5>View All</h5>
                </div>
              </div>
            </div>
          </card>
        </a>
      </swiper-slide>
      <div
        class="swiper-button-prev button-prev d-none d-lg-block btn-secondary text-center mt-n5"
        slot="button-prev"
      >
        <i class="ni ni-bold-left align-middle"></i>
      </div>
      <div
        class="swiper-button-next button-next d-none d-lg-block btn-secondary text-center mt-n5"
        slot="button-next"
      >
        <i class="ni ni-bold-right align-middle"></i>
      </div>
      <div class="swiper-pagination d-none d-md-block" slot="pagination"></div>
    </swiper>
  </div>
</template>
<script>
import "swiper/dist/css/swiper.css";

import { swiper, swiperSlide } from "vue-awesome-swiper";

import LessonCard from "@/components/Dashboard/LessonCard";
import BaseButton from "@/components/BaseButton";

export default {
  name: "lessonDeck",
  props: {
    title: {
      type: String,
      description: "Lesson Deck Area Title"
    },
    lessonData: {
      type: Array,
      description: "Lesson data to show"
    },
    viewMoreUrl: {
      type: String,
      description: "Link to View More Lessons"
    },
    courseId: {
      type: Number,
      description: "Course ID"
    }
  },
  components: {
    swiper,
    swiperSlide,
    LessonCard,
    BaseButton
  },
  data() {
    return {
      swiperOption: {
        slidesPerView: 4.2,
        spaceBetween: 20,
        freeMode: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        breakpoints: {
          1024: {
            slidesPerView: 4.2,
            spaceBetween: 20
          },
          768: {
            slidesPerView: 3.2,
            spaceBetween: 15
          },
          640: {
            slidesPerView: 1.2,
            spaceBetween: 10
          },
          320: {
            slidesPerView: 1.2,
            spaceBetween: 5
          }
        }
      }
    };
  },
  methods: {
    dropCourse: async function() {
      this.$emit("dropthis");
      this.$notify({
        type: "warning",
        message: `Removed course: ${this.title}`
      });
      await this.$http.post(
        process.env.VUE_APP_ROOT_API + "/api/v1/dashboard/toggle-course",
        {
          courseId: this.courseId,
          status: false
        }
      );
    }
  }
};
</script>
<style lang="scss" scoped>
.cpod-symbol {
  width: 50px;
}
.button-prev,
.button-next {
  transition: opacity 0.5s;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  line-height: 35px;
  font-size: 2rem;
  background: rgba(255, 255, 255, 0.7) none;
  color: #2487c1;
}
.swiper-button-disabled {
  display: none !important;
}
</style>
