import LanguageHelper from "@/helpers/LanguageHelper";
import DictationHelper from "@/helpers/DictationHelper";
import convertPinyinTones from "pinyin-tone-converter";

export default {
  getScore(question, answer) {
    if (answer === null) {
      return 0;
    }

    const type = answer.type;
    const correctAnswer = question.answers;
    const userAnswer = answer.answer;

    const totalScore = Number.parseInt(question.score);

    if (type === "multiple-choice") {
      // TODO: check multiple answers
      if (userAnswer[0] && question.answer.id === userAnswer[0]) {
        return totalScore;
      }
    }

    if (type === "matching") {
      const parts = userAnswer.length;

      const score = userAnswer.reduce((sum, line) => {
        if (line.from && line.to && line.from === line.to) {
          return sum + 1;
        }

        return sum;
      }, 0);

      return (totalScore / parts) * score;
    }

    if (type === "reorder-sentence") {
      const tags = userAnswer.map(sentence => {
        return sentence.id;
      });

      let score = 0;

      for (let i = 1; i < tags.length; i++) {
        if (Number.parseInt(tags[i]) === Number.parseInt(tags[i - 1]) + 1) {
          score++;
        }
      }

      return (totalScore / (tags.length - 1)) * score;
    }

    if (type === "dictation") {
      const sentence = question.answer[answer.language.slice(0, 1)];

      if (
        DictationHelper.removeNonChineseCharacters(sentence) ===
          DictationHelper.removeNonChineseCharacters(userAnswer) ||
        DictationHelper.normalizeSentence(question.answer["p"]) ===
          DictationHelper.normalizeAnswer(userAnswer) ||
        question.answer["p"].split(" ").join("") ===
          convertPinyinTones(userAnswer)
      ) {
        return totalScore;
      }

      return 0;
    }

    return 0;
  }
};
