import Vue from "vue";
import camelCase from "lodash.camelcase";
import MultipleChoiceExercise from "@/components/Exercises/Types/MultipleChoiceExercise";
import ReorderSentenceExercise from "@/components/Exercises/Types/ReorderSentenceExercise";
import DictationExercise from "@/components/Exercises/Types/DictationExercise";
import MatchingExercise from "@/components/Exercises/Types/MatchingExercise";
import ExerciseTypes from "@/helpers/ExerciseTypes";
import store from "@/store";

const multipleChoiceExerciseElement = Vue.extend(MultipleChoiceExercise);
const reorderSentenceExerciseElement = Vue.extend(ReorderSentenceExercise);
const dictationExerciseElement = Vue.extend(DictationExercise);
const matchingExerciseElement = Vue.extend(MatchingExercise);

export default {
  getElement(type, props) {
    const elements = {
      multipleChoiceExerciseElement,
      reorderSentenceExerciseElement,
      dictationExerciseElement,
      matchingExerciseElement
    };

    const typeCamelCase = camelCase(type);

    const index = ExerciseTypes.types.findIndex(x => {
      return x.key === type;
    });

    if (index !== -1) {
      return new elements[`${typeCamelCase}ExerciseElement`]({
        store,
        propsData: {
          ...props
        }
      });
    }

    throw new Error(
      "Can't create an instance of exercise element. Wrong exercise type!"
    );
  }
};
