  &.is-answer {
    .answer {
      color: black;
    }

    &.correct {
      .answer {
        background: #beffc3;
      }
    }

    &.wrong {
      .button {
        border-bottom-left-radius: 0;
      }

      .answer {
        background: #fdbfbf;
        border-bottom-right-radius: 0;
      }
    }
  }
}

