export default {
  getNumber(name) {
    switch (name) {
      case "simplified":
        return 1;

      case "traditional":
        return 2;

      case "pinyin":
        return 3;

      default:
        return 1;
    }
  },
  getLanguage(id) {
    switch (id) {
      case 1:
        return "simplified";

      case 2:
        return "traditional";

      case 3:
        return "pinyin";

      default:
        return "simplified";
    }
  }
};
