<template>
  <div>
    <h2 class="display-3">{{ title }}</h2>
    <swiper :options="swiperOption" ref="courseSwiper">
      <swiper-slide v-for="course in courseData" :key="course.id">
        <course-card :course="course" @addedcourse="addCourse(course)" />
      </swiper-slide>
      <swiper-slide v-if="viewMore">
        <a :href="viewMoreUrl">
          <card type="frame">
            <div class="d-flex align-items-center">
              <div class="col text-center py-3 py-lg-2">
                <img
                  src="@/assets/img/brand/symbol.svg"
                  class="img-fluid cpod-symbol"
                  alt="ChinesePod"
                />
                <div class="text-center mt-2 mb-0">
                  <h5>View All</h5>
                </div>
              </div>
            </div>
          </card>
        </a>
      </swiper-slide>
      <div
        class="swiper-button-prev button-prev d-none d-lg-block btn-secondary text-center mt-n5"
        slot="button-prev"
      >
        <i class="ni ni-bold-left align-middle"></i>
      </div>
      <div
        class="swiper-button-next button-next d-none d-lg-block btn-secondary text-center mt-n5"
        slot="button-next"
      >
        <i class="ni ni-bold-right align-middle"></i>
      </div>
      <div
        v-if="viewMore"
        class="d-none d-md-block swiper-pagination"
        slot="pagination"
      ></div>
    </swiper>
  </div>
</template>
<script>
import "swiper/dist/css/swiper.css";

import { swiper, swiperSlide } from "vue-awesome-swiper";
import CourseCard from "./CourseCard";

export default {
  name: "courseDeck",
  props: {
    title: {
      type: String,
      description: "Course Deck Area Title"
    },
    courseData: {
      type: Array,
      description: "Course data to show"
    },
    viewMore: {
      type: Boolean,
      default: true
    },
    viewMoreUrl: {
      type: String,
      description: "Link to View More Courses"
    }
  },
  components: {
    CourseCard,
    swiper,
    swiperSlide
  },
  methods: {
    addCourse(course) {
      this.$emit("addedcourse", course);
    }
  },
  data() {
    return {
      swiperOption: {
        slidesPerView: 6.2,
        spaceBetween: 20,
        freeMode: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev"
        },
        breakpoints: {
          1024: {
            slidesPerView: 6.2,
            spaceBetween: 20
          },
          768: {
            slidesPerView: 4.2,
            spaceBetween: 15
          },
          640: {
            slidesPerView: 2.2,
            spaceBetween: 10
          },
          320: {
            slidesPerView: 2.2,
            spaceBetween: 5
          }
        }
      }
    };
  }
};
</script>
<style lang="scss" scoped>
.cpod-symbol {
  width: 50px;
}
.button-prev,
.button-next {
  transition: opacity 0.5s;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  line-height: 35px;
  font-size: 2rem;
  background: rgba(255, 255, 255, 0.7) none;
  color: #2487c1;
}
.swiper-button-disabled {
  display: none !important;
}
</style>
