<template>
  <div @click="animate" :aria-label="data">
    <span
      v-for="(character, i) in data"
      :key="i"
      :id="`${rowIndex}-${i}-${character}`"
      class="characters"
      >{{ isPunctuation(character) ? character : "" }}</span
    >
  </div>
</template>

<script>
import HanziWriter from "hanzi-writer";

export default {
  name: "CharacterAnimation",
  props: {
    data: String,
    rowIndex: Number
  },
  data() {
    return {
      animations: []
    };
  },
  methods: {
    asyncForEach: async (array, callback) => {
      for (let index = 0; index < array.length; index++) {
        await callback(array[index], index, array);
      }
    },
    isPunctuation(character) {
      return new RegExp(
        `[!"\#$%&'()*+,\-./:;<=>?@\[\\\]^_\`{|}~、，。：·～！¥…（）—；【】「」《》？ˋ．・]|\\s|[A-z]|[0-9]|\\d+`
      ).test(character);
    },
    async animate() {
      await this.asyncForEach(this.animations, character =>
        character.hideCharacter({ duration: 0 })
      );
      await this.asyncForEach(this.animations, async character => {
        await character.animateCharacter({ onComplete: async function() {} });
      });
    }
  },
  mounted() {
    this.data.split("").forEach((character, index) => {
      if (!this.isPunctuation(character)) {
        try {
          this.animations.push(
            HanziWriter.create(
              `${this.rowIndex}-${index}-${character}`,
              character,
              {
                width: 40,
                height: 40,
                padding: 0,
                showOutline: true
              }
            )
          );
        } catch (e) {
          bugsnagClient.notify(e);
        }
      }
    });
  }
};
</script>

<style lang="scss" scoped>
.characters {
  font-size: 30px;
}
</style>
