<template>
  <div>
    <div>
      <base-button class="float-right" size="sm" @click="toggleCharSet">{{
        charSet === "simplified" ? "Simp: 汉字" : "Trad: 漢字"
      }}</base-button>
      <h2 class="h2">Dialogue</h2>
    </div>
    <div>
      <dialogue-item
        v-for="(sentence, index) in dialogueSegments"
        :key="index"
        :sentence-index="index"
        :speaker="sentence.speaker"
        :cn="sentence.sentence"
        :en="sentence.row_2"
        tab="dialogue"
        :audioSource="audioSource(sentence)"
        class="col-lg-7"
        :class="index & 1 ? 'float-right' : 'float-left'"
      />
    </div>
    <upgrade
      v-if="!premium && truncateData"
      hide-bullets
      class="col-12 m-0 p-0"
      title="Examples have been shortened. Upgrade to Access All Premium Features."
    />
  </div>
</template>

<script>
import DialogueItem from "./DialogueItem";
import Upgrade from "./Upgrade";

export default {
  name: "Dialogue",
  components: { Upgrade, DialogueItem },
  props: {
    lessonId: String,
    dialogueData: Object,
    hash_code: String,
    extra: Boolean
  },
  computed: {
    access() {
      return this.$store.state.userInfo
        ? this.$store.state.userInfo.access
        : "free";
    },
    premium() {
      return ["premium", "admin"].includes(this.access);
    },
    charSet() {
      return this.$store.state.userInfo
        ? this.$store.state.userInfo.charSet
        : "simplified";
    },
    audioRoot() {
      return (
        process.env.VUE_APP_ROOT_S3 +
        (this.extra ? "/extra/" : "/") +
        `${this.lessonId}/${this.hash_code}`
      );
    },
    dialogueSegments() {
      return !this.truncateData
        ? this.dialogueData.dialogue
        : this.dialogueData.dialogue.slice(0, 2);
    },
    upgradePath() {
      return this.$store.state.userInfo.upgrade.upgradePath;
    },
    truncateData() {
      return !this.premium && this.upgradePath === 2;
    }
  },
  methods: {
    async toggleCharSet() {
      await this.$store.dispatch(
        "changeCharSet",
        this.charSet === "simplified" ? "traditional" : "simplified"
      );
    },
    audioSource(sentence) {
      return sentence.audio.slice(0, 4) === "http"
        ? sentence.audio
        : process.env.VUE_APP_ROOT_S3 +
            (this.extra ? "/extra/" : "/") +
            `${this.lessonId}/${this.hash_code}` +
            "/" +
            sentence.audio;
    }
  }
};
</script>

<style scoped></style>
