<template>
  <div>
    <base-header class="pb-md-6" type="">
      <div class="row align-items-center py-4">
        <div class="col-9 col-lg-10">
          <router-link to="/home" class="d-inline-block d-md-none mr-2"
            ><i class="fas fa-home"></i> - Dashboard</router-link
          >
          <h1 class="h2 d-inline-block mb-0 mr-md-4">Explore Lessons</h1>
          <nav aria-label="breadcrumb" class="d-none d-md-inline-block">
            <ol class="breadcrumb breadcrumb-links">
              <li class="breadcrumb-item">
                <router-link to="/"><i class="fas fa-home"></i></router-link>
              </li>
              <li class="breadcrumb-item active" aria-current="page">
                <router-link to="/explore">Explore</router-link>
              </li>
            </ol>
          </nav>
        </div>
      </div>
    </base-header>
    <div class="container-fluid mt-md--6">
      <div id="searchArea" class="col-12 col-lg-8 offset-lg-2">
        <base-input group class="mb-3" prepend-icon="fas fa-search">
          <input
            type="search"
            class="form-control"
            placeholder="Search lessons, courses, topics or hosts"
            aria-label="Search lessons, courses, topics or hosts"
            aria-describedby="button-addon2"
            v-model.trim="searchTerm"
            @search="
              $router.push({ name: 'explore', query: { search: searchTerm } })
            "
          />
          <div class="input-group-append">
            <base-button
              class="btn btn-outline-primary is-loading"
              type="button"
              id="button-addon2"
              @click="
                $router.push({ name: 'explore', query: { search: searchTerm } })
              "
              :loading="searching"
              >Search</base-button
            >
          </div>
        </base-input>
      </div>
      <div v-show="page === 1" id="courses">
        <transition name="fade">
          <course-deck
            v-show="searchResults.courses && searchResults.courses.length > 0"
            title="Courses"
            :courseData="searchResults.courses"
            :viewMore="false"
          />
        </transition>
      </div>
      <div id="lessons">
        <transition name="fade">
          <div v-if="showLatestLessons && !searching">
            <div v-if="latestData && latestData.length > 0">
              <h2 class="display-3">Latest Lessons</h2>
              <div class="row">
                <lesson-card
                  v-for="lesson in latestData"
                  :key="lesson.id"
                  :lesson="lesson"
                  class="col-6 col-md-3"
                ></lesson-card>
              </div>
            </div>
          </div>
        </transition>
        <transition name="fade">
          <div v-if="!showLatestLessons">
            <div
              v-if="searchResults.lessons && searchResults.lessons.length > 0"
            >
              <h2 class="display-3">Lessons</h2>
              <div class="row">
                <lesson-card
                  v-for="lesson in searchResults.lessons.slice(
                    (page - 1) * 16,
                    page * 16
                  )"
                  :key="lesson.id"
                  :lesson="lesson"
                  :hide-buttons="true"
                  class="col-6 col-md-3"
                ></lesson-card>
              </div>
            </div>
            <div v-else-if="!searching">
              <div class="pt-4">
                Sorry! We couldn't find any matching lessons.
              </div>
            </div>
          </div>
        </transition>
        <transition name="fade">
          <div id="controls" class="text-center">
            <base-button
              v-show="showLoadMore"
              :loading="searching"
              @click="searchLessons(searchTerm, true)"
              >Load More</base-button
            >
            <base-pagination
              v-show="showPagination && !searching"
              :page-count="pages"
              :per-page="16"
              :value="page"
              align="center"
              @input="page = $event"
            ></base-pagination>
          </div>
        </transition>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";

import LessonCard from "../../components/Dashboard/LessonCard";
import CourseDeck from "../../components/Dashboard/CourseDeck";

export default {
  name: "Explore",
  components: { CourseDeck, LessonCard },
  data() {
    return {
      searchTerm: "",
      searchResults: [],
      searching: false,
      enableCourseSearch: true,
      page: 1,
      pages: 1,
      showLoadMore: false,
      showPagination: false,
      showLatestLessons: true
    };
  },
  computed: {
    ...mapState({
      courses: "courses",
      latestData: "latestData"
    })
  },
  methods: {
    logout(event) {
      console.log(event);
    },
    ...mapActions({
      updateLatestLessons: "updateLatestLessons"
    }),
    async searchLessons(query, full = false) {
      this.showPagination = false;
      if (!full) {
        this.showLoadMore = false;
      }
      if (query && query.length > 0) {
        this.searching = true;
        this.showLatestLessons = false;
        this.searchResults = await this.axios
          .get(
            process.env.VUE_APP_ROOT_API +
              "/api/v1/search/search-lessons?query=" +
              query +
              (full ? "&full" : "")
          )
          .then(({ data }) => {
            data.courses.forEach(course => {
              course.enrolled =
                this.courses.filter(item => item.course.id === course.id)
                  .length > 0;
            });
            if (full) {
              this.pages = Math.ceil(data.lessons.length / 16);
              this.showLoadMore = false;
              this.showPagination = true;
            } else {
              this.pages = 1;
              if (data.lessons.length === 12) {
                this.showLoadMore = true;
              }
            }
            return data;
          })
          .catch(() => {});
        this.searching = false;
      } else {
        this.searching = true;
        this.pages = 1;
        this.showLoadMore = false;
        this.updateLatestLessons();
        this.showLatestLessons = true;
        this.searching = false;
      }
      this.page = 1;
    }
  },
  async mounted() {
    if (this.$route.query && this.$route.query.search) {
      this.searchTerm = this.$route.query.search;
      await this.searchLessons(this.$route.query.search);
    } else {
      await this.searchLessons();
    }
  },
  async beforeRouteUpdate(to, from, next) {
    if (to.query && to.query.search && to.query.search !== from.query.search) {
      this.searchTerm = to.query.search;
      await this.searchLessons(to.query.search);
    } else {
      this.searchTerm = "";
      await this.searchLessons();
    }
    next();
  }
};
</script>

<style scoped></style>
