<template>
  <div class="media media-comment">
    <div v-lazy-container="{ selector: 'img' }">
      <img
        alt="Profile picture"
        class="avatar avatar-lg media-comment-avatar rounded-circle bg-light"
        :data-src="userImage"
        :data-loading="fallbackLogo"
        :data-error="fallbackLogo"
        @error.prevent=""
      />
    </div>
    <div class="media-body col-12">
      <div class="media-comment-text">
        <h6 class="h5 mt-0">{{ userName }}</h6>
        <p class="text-sm lh-160 comment-item" v-html="text"></p>
        <div class="icon-actions">
          <!--          <a href="#" class="like active">-->
          <!--            <i class="fas fa-thumbs-up"></i>-->
          <!--            <span class="text-muted">{{likeCount}} likes</span>-->
          <!--          </a>-->
          <a
            v-if="access !== 'free'"
            @click.prevent="$emit('reply', commentData)"
            class="like active"
            href=""
          >
            <i class="fas fa-reply"></i>
            <span class="text-muted">Reply</span>
          </a>
          <a
            v-if="admin || ownComment"
            @click.prevent="$emit('edit', commentData)"
            href=""
          >
            <i class="fas fa-edit"></i>
            <span class="text-muted">Edit</span>
          </a>
          <a
            v-if="admin || ownComment"
            @click.prevent="$emit('delete', commentData)"
            href=""
          >
            <i class="fas fa-trash-alt"></i>
            <span class="text-muted">Delete</span>
          </a>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import fallbackLogo from "@/assets/img/brand/symbol-black-center.svg";

export default {
  name: "comment",
  props: {
    userImage: {
      type: String,
      default: "/home/img/brand/symbol-black-center.svg"
    },
    userId: [String, Number],
    userName: {
      type: String
    },
    text: {
      type: String
    },
    likeCount: {
      type: Number,
      default: 0
    },
    shareCount: {
      type: Number,
      default: 0
    },
    commentData: Object
  },
  computed: {
    admin() {
      return this.$store.state.userInfo.access === "admin";
    },
    access() {
      return this.$store.state.userInfo
        ? this.$store.state.userInfo.access
        : "free";
    },
    ownComment() {
      return this.$store.state.userInfo.userId === this.userId;
    }
  },
  methods: {},
  data() {
    return {
      fallbackLogo: fallbackLogo
    };
  }
};
</script>
