<template>
  <div class="exercise-block">
    <exercise-header
      title="Re-order the segments"
      :index="index"
      :is-answer="isAnswer"
      :total-score="totalScore"
      :exercise-score="exercise.score"
      :user-score="userScore"
    ></exercise-header>
    <draggable
      class="draggable-sentences"
      v-if="!isAnswer"
      v-model="orderedSentences"
      group="draggable-sentences"
      ghost-class="ghost"
      :animation="10"
      :disabled="false"
      @start="startDrag"
      @end="endDrag"
    >
      <transition-group name="ordered-sentences">
        <card
          v-for="(sentence, index) in orderedSentences"
          :key="sentence.id"
          class="bg-gradient-lighter"
          body-classes="row"
        >
          <div class="col-10 col-sm-11">
            <span class="characters">{{ sentence[charSet.slice(0, 1)] }}</span>
            <footer v-if="charSet !== 'pinyin' && pinyin" class="pinyin">
              {{ sentence["p"] }}
            </footer>
          </div>
          <div class="col-2 col-sm-1">
            <div class="row">
              <div class="col-12">
                <base-button
                  type="secondary"
                  size="lg"
                  v-show="index !== 0"
                  class="btn-icon-only mb-2"
                  @click="moveUp(index)"
                  ><i class="text-lg fa fa-chevron-up"></i
                ></base-button>
              </div>
            </div>
          </div>
        </card>
      </transition-group>
    </draggable>
    <div class="answers" v-if="isAnswer">
      <card
        v-for="sentence in solution"
        :key="sentence.id"
        :type="isCorrect(sentence.id) ? 'success' : 'danger'"
        body-classes="row"
      >
        <div class="col-10 col-sm-11">
          <span class="characters text-white">{{
            sentence[charSet.slice(0, 1)]
          }}</span>
          <footer
            v-if="charSet !== 'pinyin' && pinyin"
            class="pinyin text-white"
          >
            {{ sentence["p"] }}
          </footer>
        </div>
        <div class="col-2 col-sm-1">
          <div class="row align-items-center">
            <div class="col-12 align-items-center">
              <badge size="md" type="default" class="mt-0 text-sm">{{
                sentence.id
              }}</badge>
            </div>
          </div>
        </div>
      </card>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import LanguageNumber from "@/mixins/LanguageNumber";
import Exercise from "@/mixins/Exercise";
import ExerciseHeader from "@/components/Exercises/ExerciseHeader";
import draggable from "vuedraggable";
import shuffle from "lodash.shuffle";

export default {
  name: "ReorderSentenceExercise",
  components: {
    ExerciseHeader,
    draggable
  },
  mixins: [LanguageNumber, Exercise],
  data() {
    return {
      orderedSentences: [],
      drag: false
    };
  },
  computed: {
    solution() {
      return this.answer.answer;
    },
    ...mapState({
      pinyin: state => state.userInfo.pinyin,
      charSet: state => state.userInfo.charSet
    })
  },
  mounted() {
    this.orderedSentences = shuffle(this.exercise.question.segments);
  },
  methods: {
    startDrag() {
      this.drag = true;
    },
    endDrag() {
      this.drag = false;

      this.emitAnswer();
    },
    isCorrect(tag) {
      const answers = this.answer.answer;

      const index = answers.findIndex(x => x.id === tag);

      if (index >= 0) {
        return index + 1 === answers[index].id;
      }

      return false;
    },
    isWrong(tag) {
      return !this.isCorrect(tag);
    },
    moveUp(index) {
      const list = this.orderedSentences.slice();
      if (index > 0) {
        this.drag = true;
        const a = list[index];
        const b = list[index - 1];

        list[index - 1] = a;
        list[index] = b;

        this.orderedSentences = list;
        this.drag = false;
      }
      this.emitAnswer();
    },
    moveDown(index) {
      const list = this.orderedSentences.slice();
      //
      // const index = list.findIndex(({id}) => id === tag);

      if (index < list.length - 1) {
        this.drag = true;
        const a = list[index];
        const b = list[index + 1];

        list[index + 1] = a;
        list[index] = b;

        this.orderedSentences = list;
        this.drag = false;
      }

      this.emitAnswer();
    },
    emitAnswer() {
      this.$emit("answer", {
        type: "reorder-sentence",
        answer: this.orderedSentences,
        id: this.exercise.id,
        language: this.charSet,
        completed: true
      });
    }
  }
};
</script>

