export default {
  types: [
    {
      key: "multiple-choice",
      name: "Multiple choice"
    },
    {
      key: "matching",
      name: "Matching"
    },
    {
      key: "reorder-sentence",
      name: "Sentence re-ordering"
    },
    {
      key: "dictation",
      name: "Dictation"
    }
  ],
  getType(count) {
    if (count >= this.types.length) {
      throw new Error();
    }

    return this.types[count].key;
  },
  getNextType(type) {
    const currentTypeIndex = this.types.findIndex(x => {
      return x.key === type;
    });

    if (currentTypeIndex < this.types.length - 1) {
      return this.types[currentTypeIndex + 1].key;
    }

    return "";
  }
};
