<template>
  <div class="summary">
    <h1 class="py-2">
      Score: {{ totalScore }}/{{ maxScore }}, {{ percentage }}
    </h1>
    <div
      class="types"
      v-for="type in types"
      :key="type.key"
      v-show="exerciseLength(type.key)"
    >
      <h2>
        {{ type.name }}: {{ getUserScore(type.key) }} /
        {{ totalScores[type.key] }} {{ pointsText(type.key) }}
      </h2>
      <div class="answer-list">
        <div
          class="answer"
          v-for="answer in getHistoryForType(type.key)"
          :key="answer.id"
        >
          <keep-alive>
            <exercise
              :exercise="getExercise(answer.type, answer.id)"
              :answer="answer"
            ></exercise>
          </keep-alive>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import ExerciseTypes from "@/helpers/ExerciseTypes";
import Answers from "@/helpers/Answers";
import camelCase from "lodash.camelcase";
import Exercise from "@/components/Exercises/Exercise";

export default {
  name: "Summary",
  components: {
    Exercise
  },
  props: {
    lessonId: [Number, String]
  },
  data() {
    return {
      totalScores: {},
      userScores: {},
      types: ExerciseTypes.types
    };
  },
  computed: {
    totalScore() {
      let totalScore = 0;

      for (let type of this.types) {
        totalScore += this.getUserScore(type.key);
      }

      return totalScore;
    },
    maxScore() {
      let maxScore = 0;

      const keys = Object.keys(this.totalScores);

      for (let key of keys) {
        if (this.totalScores[key]) {
          maxScore += this.totalScores[key];
        }
      }

      return maxScore;
    },
    percentage() {
      if (this.maxScore !== 0) {
        const percentage = (this.totalScore / this.maxScore) * 100;

        return `${percentage.toFixed(2)}%`;
      }

      return "0%";
    },
    multipleChoiceExercise() {
      if (this.multipleChoiceExerciseData) {
        return this.multipleChoiceExerciseData;
      }

      return null;
    },
    reorderSentenceExercise() {
      if (this.reorderSentenceExerciseData) {
        return this.reorderSentenceExerciseData;
      }

      return null;
    },
    dictationExercise() {
      if (this.dictationExerciseData) {
        return this.dictationExerciseData;
      }

      return null;
    },
    matchingExercise() {
      if (this.matchingExerciseData) {
        return this.matchingExerciseData;
      }

      return null;
    },
    ...mapState({
      multipleChoiceExerciseData: state =>
        state.exercises.multipleChoiceQuestionData,
      reorderSentenceExerciseData: state =>
        state.exercises.sentenceQuestionData,
      dictationExerciseData: state => state.exercises.dictationQuestionData,
      matchingExerciseData: state => state.exercises.matchingQuestionData,
      multipleChoiceQuestionCount: state =>
        state.exercises.multipleChoiceQuestionCount,
      reorderSentenceQuestionCount: state =>
        state.exercises.sentenceQuestionCount,
      dictationQuestionCount: state => state.exercises.dictationQuestionCount,
      matchingQuestionCount: state => state.exercises.matchingQuestionCount,
      history: state => state.exercises.history
    })
  },
  mounted() {
    if (
      this["multipleChoiceExercise"] !== null &&
      this["reorderSentenceExercise"] !== null &&
      this["dictationExercise"] !== null &&
      this["matchingChoiceExercise"] !== null
    ) {
      this.createQuestionList();
      this.getTotalScores();
      this.postResults();
    } else {
      this.$watch(
        () => {
          return (
            this["multipleChoiceExercise"] !== null &&
            this["reorderSentenceExercise"] !== null &&
            this["dictationExercise"] !== null &&
            this["matchingChoiceExercise"] !== null
          );
        },
        () => {
          this.createQuestionList();
          this.getTotalScores();
        }
      );
    }
  },
  methods: {
    exerciseLength(type) {
      return this[`${camelCase(type)}QuestionCount`] > 0;
    },
    getExercise(type, id) {
      return this[`${camelCase(type)}Exercise`].filter(x => x.id === id)[0];
    },
    getHistoryForType(type) {
      const result = this.history.filter(x => {
        return x.type === type;
      });

      return result;
    },
    createQuestionList() {
      const list = [];

      return list;
    },
    getTotalScores() {
      const types = ExerciseTypes.types;

      const scores = {};

      for (let type of types) {
        scores[type.key] = this.getTotalScoreForType(type.key);
      }

      this.totalScores = scores;
    },
    getTotalScoreForType(type) {
      const dataField = `${camelCase(type)}Exercise`;

      if (this[dataField] === undefined) {
        return 0;
      }

      return this[dataField].reduce((acc, question) => {
        return acc + Number.parseInt(question.score);
      }, 0);
    },
    getUserScore(type) {
      const answers = this.history.filter(x => x.type === type);

      let score = 0;

      const dataField = `${camelCase(type)}Exercise`;

      for (let answer of answers) {
        const question = this[dataField].filter(x => x.id === answer.id)[0];

        if (typeof question !== "undefined" && question.score !== undefined) {
          score += Answers.getScore(question, answer);
        }
      }

      return score;
    },
    pointsText(key) {
      if (this.totalScores[key] > 1) {
        return "points";
      }

      return "point";
    },
    async postResults() {
      await this.axios
        .post(process.env.VUE_APP_ROOT_API + `/api/v1/exercises/results`, {
          lessonId: this.lessonId,
          totalScore: this.maxScore,
          userScore: this.totalScore
        })
        .catch();
    }
  }
};
</script>

