<template>
  <!--  <a :href="`https://chinesepod.com/library/courses/${course.course_title.toLowerCase().split(' ').join('-')}/${course.id}`" class="card bg-transparent text-white border-0 mb-5">-->
  <div class="card bg-transparent border-0 mb-5">
    <router-link :to="{ name: 'course', params: { courseId: course.id } }">
      <div v-lazy-container="{ selector: 'img' }">
        <img
          class="card-img"
          :data-src="
            `https://chinesepod.com/images/courses/${course.id}/course.svg`
          "
          :data-loading="preloaderSquare"
          :data-error="preloaderSquare"
          :alt="course.course_title"
        />
      </div>
      <div
        class="card-img-overlay rounded shadow-none"
        :class="[course.enrolled ? 'bg-translucent-dark' : 'bg-transparent']"
      >
        <div class="float-right">
          <button
            class="btn btn-secondary btn-circle btn-circle-sm mt--4 mr--3"
            :class="[course.enrolled ? 'text-danger' : '']"
            @click.prevent="enrollCourse(course)"
          >
            <i
              class="fas"
              :class="[course.enrolled ? 'fa-minus' : 'fa-plus']"
            ></i>
          </button>
        </div>
      </div>
    </router-link>
    <!--    </div>-->
    <!--  </el-popover>-->
  </div>
  <!--  </a>-->
</template>

<script>
import preloaderSquare from "@/assets/img/preloader-square.svg";

export default {
  name: "CourseCard",
  props: {
    course: {
      type: Object,
      description: "Course data to show"
    }
  },
  data() {
    return {
      preloaderSquare: preloaderSquare
    };
  },
  methods: {
    enrollCourse: async function(course) {
      course.enrolled = !course.enrolled;
      this.$forceUpdate();
      if (course.enrolled) {
        this.$notify({
          type: "success",
          message: `Added course: ${course.course_title}`
        });
      } else {
        this.$notify({
          type: "warning",
          message: `Removed course: ${course.course_title}`
        });
      }
      this.$emit("addedcourse");
      await this.$http.post(
        process.env.VUE_APP_ROOT_API + "/api/v1/dashboard/toggle-course",
        {
          courseId: course.id,
          status: course.enrolled
        }
      );
    }
  }
};
</script>

