<template>
  <div>
    <base-header class="pb-md-6" type="">
      <div class="row align-items-center py-4">
        <div
          v-if="lessonData.title"
          class="col-9 col-sm-9 col-lg-8"
          :class="[nextLessons.length > 0 || fetchingNextLessons ? '' : '']"
        >
          <router-link to="/home" class="d-inline-block d-md-none mr-2"
            ><i class="fas fa-home"></i> - Dashboard</router-link
          >
          <h1 class="h2 d-inline-block mb-0 mr-md-4">{{ lessonData.title }}</h1>
          <nav aria-label="breadcrumb" class="d-none d-md-inline-block">
            <ol class="breadcrumb breadcrumb-links">
              <li class="breadcrumb-item">
                <router-link to="/"><i class="fas fa-home"></i></router-link>
              </li>
              <li class="breadcrumb-item">
                <router-link to="/home">Dashboard</router-link>
              </li>
              <li class="breadcrumb-item active" aria-current="page">
                {{ lessonData.title }}
              </li>
            </ol>
          </nav>
        </div>
        <div
          v-if="lessonData.title"
          class="buttons col-3 col-sm-3 col-lg-4"
          :class="[nextLessons.length > 0 || fetchingNextLessons ? '' : '']"
        >
          <div class="row">
            <div class="col-sm-6 mb-2">
              <base-button
                class="btn-icon btn-block"
                type="secondary"
                :class="[saved ? 'text-danger' : '']"
                @click="markSaved"
              >
                <i class="fa-bookmark m-0" :class="[saved ? 'fas' : 'far']"></i>
                <span class="d-none d-lg-block"
                  >&nbsp;Save<span v-show="saved">d</span></span
                >
              </base-button>
            </div>
            <div class="col-sm-6">
              <base-button
                class="btn-icon btn-block"
                type="secondary"
                :class="[studied ? 'text-danger' : '']"
                @click="markStudied"
              >
                <i
                  class="fa-check-circle m-0"
                  :class="[studied ? 'fas' : 'far']"
                ></i>
                <span class="d-none d-lg-block"
                  >&nbsp;Complete<span v-show="studied">d</span></span
                >
              </base-button>
            </div>
          </div>
        </div>
      </div>
    </base-header>
    <!--    LESSON CONTENT-->
    <div class="container-fluid mt-md--6">
      <div class="row">
        <div class="col-12">
          <div class="row">
            <div
              class="col-sm-8"
              :class="[
                nextLessons.length > 0 || fetchingNextLessons
                  ? ''
                  : 'offset-sm-2'
              ]"
            >
              <div
                v-if="!lessonData.title"
                class="col-12 d-flex align-items-center pt-4 pt-sm-0 lesson-loading"
              >
                <i class="fas fa-spinner fa-spin mx-auto"></i>
              </div>
              <transition name="fade">
                <lesson-media
                  v-if="lessonData.title"
                  :key="lessonData.id"
                  :lesson-data="lessonData"
                />
              </transition>
            </div>
            <div
              v-if="lessonData.title && fetchingNextLessons"
              class="col-sm-4 d-flex align-items-center pt-4 pt-sm-0"
            >
              <i class="fas fa-spinner fa-spin mx-auto"></i>
            </div>
            <div
              v-if="!fetchingNextLessons && nextLessons.length > 0"
              class="col-sm-4 pt-4 pt-sm-0"
            >
              <h3>Next Lessons...</h3>
              <next-lessons :lessonData="nextLessons" :course-id="courseId" />
            </div>
          </div>
          <div v-if="lessonData.title">
            <stats-card class="bg-gradient-blue my-4">
              <div class="row">
                <div class="col">
                  <h5
                    class="card-title text-uppercase text-muted mb-0 text-white"
                  >
                    In this Lesson
                    <badge
                      size="lg"
                      class="float-right text-white"
                      :class="lessonLevel"
                      >{{ lessonData.level }}</badge
                    >
                  </h5>
                  <div
                    class="card-body mb-0 text-white"
                    v-html="lessonData.introduction.trim()"
                  ></div>
                </div>
              </div>
              <template slot="footer">
                <span class="text-white text-nowrap float-right mr-2">
                  {{
                    new Date(lessonData.publication_timestamp).toDateString()
                  }}
                  | {{ lessonData.hosts.split(",").join(", ") }}
                </span>
              </template>
            </stats-card>
            <upgrade
              v-if="!premium && !bannerPath"
              hide-bullets
              allow-dismiss
            />
          </div>
          <tabs
            v-if="lessonData.id"
            tabNavClasses="nav-fill flex-sm-row nav-wrapper"
            ref="tabs"
            tabClasses="mt-2"
            tabContentClasses="card shadow"
          >
            <tab-pane
              v-if="loadData && dialogueData.dialogue.length > 0"
              id="dialogue"
            >
              <span slot="title">
                <i class="ni ni-lg icon-speaking"></i>
                <span class="d-none d-md-block">Dialogue</span>
              </span>
              <div class="card-body dialogue">
                <dialogue
                  :key="lessonData.id"
                  :lessonId="lessonData.id"
                  :hash_code="lessonData.hash_code"
                  :extra="lessonData.type === 'extra'"
                  :dialogue-data="dialogueData"
                />
              </div>
            </tab-pane>
            <tab-pane v-if="loadData && vocabData.length > 0" id="vocabulary">
              <span slot="title">
                <i class="ni ni-lg icon-vocabulary"></i>
                <span class="d-none d-md-block">Vocabulary</span>
              </span>
              <div class="card-body vocabulary">
                <vocab
                  v-if="vocabData"
                  :key="lessonData.id"
                  :lesson-id="lessonData.id"
                  :lessonData="lessonData"
                  :vocabData="vocabData"
                  :extra="lessonData.type === 'extra'"
                />
              </div>
            </tab-pane>
            <tab-pane
              v-if="loadData && expansionData.length > 0"
              id="expansion"
            >
              <span slot="title">
                <i class="ni ni-lg icon-expansion"></i>
                <span class="d-none d-md-block">Expansion</span>
              </span>
              <div class="card-body expansion">
                <expansion
                  :key="lessonData.id"
                  :lessonId="lessonData.id"
                  :hash_code="lessonData.hash_code"
                  :extra="lessonData.type === 'extra'"
                  :expansionData="expansionData"
                />
              </div>
            </tab-pane>
            <tab-pane v-if="loadData && grammarData.length > 0" id="grammar">
              <span slot="title">
                <i class="ni ni-lg ni-collection"></i>
                <span class="d-none d-md-block">Grammar</span>
              </span>
              <div class="card-body grammar">
                <grammar
                  :key="lessonData.id"
                  :lessonId="lessonData.id"
                  :hash_code="lessonData.hash_code"
                  :extra="lessonData.type === 'extra'"
                  :grammarData="grammarData"
                />
              </div>
            </tab-pane>
            <tab-pane
              v-if="
                loadData &&
                  exerciseData.choice &&
                  (exerciseData.audio.length > 0 ||
                    exerciseData.choice.length > 0 ||
                    exerciseData.matching.length > 0 ||
                    exerciseData.rearrange.length > 0)
              "
              id="exercises"
            >
              <span slot="title">
                <i class="ni ni-lg icon-exam"></i>
                <span class="d-none d-md-block">Exercises</span>
              </span>
              <div class="card-body exercises">
                <exercise
                  :key="lessonData.id"
                  :lesson-id="lessonData.id"
                  :extra="lessonData.type === 'extra'"
                  :exercise-data="exerciseData"
                />
              </div>
            </tab-pane>
            <tab-pane
              v-if="
                premium &&
                  downloadsData.type &&
                  downloadsData.type === 'premium'
              "
              id="downloads"
              class="mt-2"
            >
              <span slot="title">
                <i class="ni ni-lg ni-cloud-download-95"></i>
                <span class="d-none d-md-block">Downloads</span>
              </span>
              <div class="card-body downloads">
                <downloads
                  :key="lessonData.id"
                  :lesson-id="lessonData.id"
                  :extra="lessonData.type === 'extra'"
                  :title="lessonData.title"
                  :downloads-data="downloadsData"
                />
              </div>
            </tab-pane>
            <tab-pane v-if="commentData.comments" id="comments" class="mt-2">
              <span slot="title">
                <i class="ni ni-lg ni-chat-round"></i>
                <span class="d-none d-md-block"
                  ><badge size="lg" type="primary">{{
                    commentData.count
                  }}</badge>
                  Comments</span
                >
              </span>
              <div class="card-body comments">
                <comments
                  :key="lessonData.id"
                  :lesson-id="lessonData.id"
                  :comment-data="commentData.comments"
                  @update="getComments"
                />
              </div>
            </tab-pane>
          </tabs>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// Components
import BaseHeader from "@/components/BaseHeader";
import StatsCard from "@/components/Cards/StatsCard";
import Tabs from "@/components/Tabs/Tabs";
import TabPane from "@/components/Tabs/Tab";

import Dialogue from "../../components/Lesson/Dialogue";
import Vocab from "../../components/Lesson/Vocab";
import Downloads from "../../components/Lesson/Downloads";
import LessonMedia from "../../components/Lesson/Media";
import NextLessons from "../../components/Lesson/NextLessons";
import Expansion from "../../components/Lesson/Expansion";
import Grammar from "../../components/Lesson/Grammar";
import Comments from "../../components/Lesson/Comments";
import Upgrade from "../../components/Lesson/Upgrade";
import Exercise from "../../components/Exercises/index";

export default {
  name: "lessonDetails",
  components: {
    Exercise,
    Upgrade,
    Comments,
    Grammar,
    Expansion,
    NextLessons,
    LessonMedia,
    Downloads,
    Dialogue,
    Vocab,
    BaseHeader,
    StatsCard,
    Tabs,
    TabPane
  },
  computed: {
    isUserAuthenticated() {
      return this.$store.state.token !== "";
    },
    access() {
      return this.$store.state.userInfo
        ? this.$store.state.userInfo.access
        : "free";
    },
    premium() {
      return ["premium", "admin"].includes(this.access);
    },
    upgradePath() {
      return this.$store.state.userInfo.upgrade.upgradePath;
    },
    bannerPath() {
      return this.upgradePath === 3;
    },
    loadData() {
      return this.premium || [2, 3].includes(this.upgradePath);
    },
    charSet() {
      return this.$store.state.userInfo
        ? this.$store.state.userInfo.charSet
        : "simplified";
    },
    audioSource() {
      return (
        process.env.VUE_APP_ROOT_S3 +
        "/" +
        this.lessonData.id +
        "/" +
        this.lessonData.hash_code +
        "/" +
        this.lessonData["mp3_private"]
      );
    },
    studied() {
      return this.$store.state.currentLesson.studied;
    },
    saved() {
      return this.$store.state.currentLesson.saved;
    },
    lessonLevel() {
      return this.lessonData.level.split(" ").length > 1
        ? this.lessonData.level
            .split(" ")
            .join("-")
            .toLowerCase()
        : this.lessonData.level.toLowerCase();
    }
  },
  async beforeMount() {
    this.slug = this.$route.params.slug;
    this.courseId = this.$route.params.courseId;
    if (!this.$store.state.userInfo.access) {
      await this.$store
        .dispatch("getUserInfo")
        .catch(() => (window.location.href = "/login"));
    } else {
      this.$store
        .dispatch("getUserInfo")
        .catch(() => (window.location.href = "/login"));
    }
    await this.getThisLesson();
    if (this.courseId) {
      this.getNextLessons();
    }
    // if (this.premium) {
    if (this.loadData) {
      if (this.bannerPath) {
        this.resetBanner();
      }
      await this.getDialogue();
      await this.getVocab();
      await this.getExpansion();
      await this.getGrammar();
      await this.getExercises();
    }
    if (this.premium) {
      await this.getDownloads();
    }
    await this.getComments();
    this.setActiveTab();
    await this.$store
      .dispatch("getUserInfo")
      .catch(() => (window.location.href = "/login"));
  },
  async beforeRouteUpdate(to, from, next) {
    this.slug = to.params.slug;
    this.courseId = to.params.courseId;
    this.dialogueData = { dialogue: [] };
    this.vocabData = this.expansionData = this.grammarData = [];
    this.commentData = this.downloadsData = this.exerciseData = {};
    this.$store
      .dispatch("getUserInfo")
      .catch(() => (window.location.href = "/login"));
    await this.getThisLesson();
    next();
    if (this.courseId) {
      this.getNextLessons();
    }
    if (this.loadData) {
      // if (this.premium) {
      if (this.bannerPath) {
        this.resetBanner();
      }
      await this.getDialogue();
      await this.getVocab();
      await this.getExpansion();
      await this.getGrammar();
      await this.getExercises();
    }
    if (this.premium) {
      await this.getDownloads();
    }
    await this.getComments();
    this.setActiveTab();
  },
  methods: {
    async getThisLesson() {
      this.lessonData = await this.axios
        .get(
          process.env.VUE_APP_ROOT_API +
            `/api/v1/lessons/get-lesson?slug=${this.slug}`
        )
        .then(({ data }) => {
          return data;
        })
        .catch(() => {
          window.location.href = "/login";
        });
      await this.$store.dispatch("currentLesson", this.lessonData);
    },
    async getNextLessons() {
      this.fetchingNextLessons = true;
      this.nextLessons = await this.axios
        .get(
          process.env.VUE_APP_ROOT_API +
            `/api/v1/dashboard/course-lessons?courseId=${this.courseId}&exclude[0]=${this.lessonData.id}&limit=2`
        )
        .then(({ data }) => {
          return data;
        })
        .catch(() => {
          return [];
        });
      this.fetchingNextLessons = false;
    },
    async getDialogue() {
      this.dialogueData = await this.axios
        .get(
          process.env.VUE_APP_ROOT_API +
            `/api/v1/lessons/get-dialogue?lessonId=${this.lessonData.id}`
        )
        .then(({ data }) => {
          return data;
        })
        .catch(() => {
          return { dialogue: [] };
        });
    },
    async getVocab() {
      this.vocabData = await this.axios
        .get(
          process.env.VUE_APP_ROOT_API +
            `/api/v1/lessons/get-vocab?lessonId=${this.lessonData.id}`
        )
        .then(({ data }) => {
          return data;
        })
        .catch(() => {
          return [];
        });
    },
    async getExpansion() {
      this.expansionData = await this.axios
        .get(
          process.env.VUE_APP_ROOT_API +
            `/api/v1/lessons/get-expansion?lessonId=${this.lessonData.id}`
        )
        .then(({ data }) => {
          return data;
        })
        .catch(() => {
          return [];
        });
    },
    async getGrammar() {
      this.grammarData = await this.axios
        .get(
          process.env.VUE_APP_ROOT_API +
            `/api/v1/lessons/get-grammar?lessonId=${this.lessonData.id}`
        )
        .then(({ data }) => {
          return data;
        })
        .catch(() => {
          return [];
        });
    },
    async getComments() {
      this.commentData = await this.axios
        .get(
          process.env.VUE_APP_ROOT_API +
            `/api/v1/lessons/get-comments?lessonId=${this.lessonData.id}`
        )
        .then(({ data }) => {
          return data;
        })
        .catch(() => {
          return {};
        });
    },
    async getDownloads() {
      this.downloadsData = await this.axios
        .get(
          process.env.VUE_APP_ROOT_API +
            `/api/v1/lessons/get-downloads?lessonId=${this.lessonData.id}`
        )
        .then(({ data }) => {
          return data;
        })
        .catch(() => {
          return {};
        });
    },
    async getExercises() {
      this.exerciseData = await this.axios
        .get(
          process.env.VUE_APP_ROOT_API +
            `/api/v1/exercises/get-questions?lessonId=${this.lessonData.id}`
        )
        .then(({ data }) => {
          return data;
        })
        .catch(() => {
          return {};
        });
    },
    resetBanner() {
      this.$store.commit("TOGGLE_BANNER", true);
    },
    markStudied: async function() {
      await this.$store.dispatch("toggleCurrentLessonStudied", {
        lessonId: this.lessonData.id,
        studied: !this.studied
      });
    },
    markSaved: async function() {
      await this.$store.dispatch("toggleCurrentLessonSaved", {
        lessonId: this.lessonData.id,
        saved: !this.saved
      });
    },
    setActiveTab() {
      if (!this.dialogueData.dialogue.length > 0 && this.vocabData.length > 0) {
        this.$refs.tabs.findAndActivateTabById("vocabulary");
      } else if (!this.dialogueData.dialogue.length > 0) {
        this.$refs.tabs.findAndActivateTabById("comments");
      }
      if (
        this.$route.hash &&
        this.$refs.tabs.listTabs().includes(this.$route.hash.slice(1))
      ) {
        this.$refs.tabs.findAndActivateTabById(this.$route.hash.slice(1));
        this.$refs.tabs.$el.scrollIntoView();
      }
    }
  },
  data() {
    return {
      lessonData: {},
      dialogueData: {
        dialogue: []
      },
      vocabData: [],
      expansionData: [],
      grammarData: [],
      exerciseData: {},
      downloadsData: {},
      commentData: {},
      nextLessons: {},
      fetchingNextLessons: false,
      slug: false,
      courseId: false,
      active: "comments"
    };
  }
};
</script>

<style scoped>
.fa-spinner {
  font-size: 60px;
}
.fade-leave-active {
  transition: all 0s;
}
.lesson-loading {
  min-height: 400px;
}
.newbie {
  background-color: #3399cc;
}
.elementary {
  background-color: #33cc66;
}
.pre-intermediate {
  background-color: #ffcc00;
}
.intermediate {
  background-color: #ff6600;
}
.upper-intermediate {
  background-color: #cc0033;
}
.advanced {
  background-color: #990066;
}
.media {
  background-color: #003333;
}
</style>
