<template>
  <div>
    <h4 v-if="speaker" class="text-uppercase ls-1 py-3 mb-0">{{ speaker }}</h4>
    <card
      class="card-pricing bg-light border-0 mb-4"
      header-classes="bg-transparent"
      footer-classes="bg-transparent"
    >
      <div slot="header" class="row align-items-center">
        <div class="col-10 col-sm-11">
          <p class="characters">
            <character-item
              v-for="(character, index) in cn"
              :key="index"
              :characterIndex="index"
              :sentenceIndex="sentenceIndex"
              :character="character"
              :type="charSet"
              :tab="`${tab}-char`"
              popup
            />
          </p>
        </div>
        <div class="col-2 col-sm-1">
          <base-button
            class="float-right btn-icon-only"
            @click="showPlayer = !showPlayer"
            type="secondary"
            ><i
              class="fas"
              :class="[showPlayer ? 'fa-times' : 'fa-volume-up']"
            ></i
          ></base-button>
        </div>
        <div class="col-12">
          <vue-plyr v-if="showPlayer" ref="plyr" :options="playerOptions">
            <audio>
              <source :src="audioSource" type="audio/mp3" />
            </audio>
          </vue-plyr>
        </div>
      </div>
      <div slot="default" class="row align-items-center">
        <div class="col-10 col-sm-11">
          <p v-show="showPinyin" class="pinyin">
            <character-item
              v-for="(character, index) in cn"
              :key="index"
              :characterIndex="index"
              :sentenceIndex="sentenceIndex"
              :character="character"
              :tab="`${tab}-pinyin`"
              type="pinyin"
            />
          </p>
        </div>
        <div class="col-2 col-sm-1">
          <base-button
            class="float-right btn-icon-only"
            @click="showPinyin = !showPinyin"
            type="secondary"
            ><i class="fas" :class="[showPinyin ? 'fa-times' : 'fa-eye']"></i
          ></base-button>
        </div>
      </div>
      <div slot="footer" class="row align-items-center">
        <div class="col-10 col-sm-11">
          <p v-show="showEng" class="english">{{ en }}</p>
        </div>
        <div class="col-2 col-sm-1">
          <base-button
            class="float-right btn-icon-only"
            @click="showEng = !showEng"
            type="secondary"
            ><i class="fas" :class="[showEng ? 'fa-times' : 'fa-eye']"></i
          ></base-button>
        </div>
      </div>
    </card>
  </div>
</template>

<script>
import Card from "@/components/Cards/Card";
import CharacterItem from "./CharacterItem";

export default {
  name: "DialogueItem",
  components: {
    CharacterItem,
    Card
  },
  props: {
    speaker: {
      type: String
    },
    sentence: {
      type: String
    },
    cn: {
      type: Array
    },
    en: {
      type: String
    },
    sentenceIndex: Number,
    audioSource: String,
    tab: String
  },
  mounted() {},
  computed: {
    access() {
      return this.$store.state.userInfo
        ? this.$store.state.userInfo.access
        : "free";
    },
    traditional() {
      return this.$store.state.userInfo.charSet === "traditional";
    },
    charSet() {
      return this.$store.state.userInfo
        ? this.$store.state.userInfo.charSet
        : "simplified";
    },
    player() {
      return this.$refs.plyr.player;
    }
  },
  data() {
    return {
      showEng: false,
      showPinyin: false,
      showPlayer: false,
      playerOptions: {
        controls: [
          "play-large",
          "play",
          "progress",
          "current-time",
          "settings"
        ],
        settings: ["captions", "quality", "speed", "loop", "autoplay"],
        loop: { active: false },
        autoplay: true
      }
    };
  }
};
</script>

<style scoped>
.characters {
  font-size: 30px;
}
.pinyin,
.english {
  font-size: 24px;
}
</style>
