<script>
/* eslint-disable no-new */
import PerfectScrollbar from "perfect-scrollbar";
import "perfect-scrollbar/css/perfect-scrollbar.css";

function hasElement(className) {
  return document.getElementsByClassName(className).length > 0;
}

function initScrollbar(className) {
  if (hasElement(className)) {
    new PerfectScrollbar(`.${className}`);
  } else {
    // try to init it later in case this component is loaded async
    setTimeout(() => {
      initScrollbar(className);
    }, 100);
  }
}

import DashboardNavbar from "./DashboardNavbar.vue";
import ContentFooter from "./ContentFooter.vue";
import { FadeTransition } from "vue2-transitions";
import Upgrade from "../../components/Lesson/Upgrade";

export default {
  components: {
    Upgrade,
    DashboardNavbar,
    ContentFooter,
    FadeTransition
  },
  title() {
    return "Dashboard | ChinesePod";
  },
  async beforeCreate() {
    if (!this.$store.state.token) {
      await this.$store.dispatch("getToken").catch(() => {
        this.$store.commit("RESET_USER_INFO");
        window.location.href = "/login";
      });
    } else {
      this.$store.commit("APPLY_TOKEN");
    }
    if (!this.$store.state.userInfo.access) {
      await this.$store.dispatch("getUserInfo").catch(async () => {
        await this.$store
          .dispatch("getToken")
          .then(async () => await this.$store.dispatch("getUserInfo"))
          .catch(() => {
            this.$store.commit("RESET_USER_INFO");
            window.location.href = "/login";
          });
      });
    }
  },
  computed: {
    banner() {
      return this.$store.state.userInfo.banner;
    },
    premium() {
      return ["premium", "admin"].includes(this.$store.state.userInfo.access);
    },
    showBanner() {
      return (
        this.$store.state.userInfo.upgrade.upgradePath === 3 && !this.premium
      );
    },
    canDismiss() {
      return this.$store.state.userInfo.upgrade.canDismiss;
    }
  },
  methods: {
    initScrollbar() {
      let isWindows = navigator.platform.startsWith("Win");
      if (isWindows) {
        initScrollbar("scrollbar-inner");
      }
    }
  },
  mounted() {
    this.initScrollbar();
  }
};
</script>
