<template>
  <div class="col-sm-6 col-xl-3">
    <card
      class="border-0 text-center bg-gray"
      header-classes="bg-transparent"
      footer-classes="bg-transparent"
    >
      <div class="row">
        <div class="col">
          <h4 class="text-uppercase ls-1 text-white py-3 mb-0">{{ title }}</h4>
        </div>
        <div class="col-auto my-auto">
          <base-button
            size="sm"
            type="light"
            @click="downloadFile(link, `${lessonId}-${name}`)"
            :disabled="downloadInProgress"
            ><i :class="icon" class="ni-lg py-2"></i
          ></base-button>
        </div>
      </div>
      <base-progress
        v-if="downloadInProgress"
        slot="footer"
        striped
        animated
        :value="progress"
        :key="progress"
        :height="10"
      />
    </card>
  </div>
</template>

<script>
import bugsnagClient from "@/lib/bugsnag";

export default {
  name: "DownloadsItem",
  props: {
    title: String,
    icon: String,
    link: String,
    lessonId: [String, Number],
    name: String
  },
  data() {
    return {
      downloadInProgress: false,
      progress: 0
    };
  },
  computed: {
    downloadLink() {
      return process.env.VUE_APP_ROOT_DOWNLOAD + this.link;
    }
  },
  methods: {
    forceFileDownload(response, filename) {
      const url = window.URL.createObjectURL(new Blob([response.data]));
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download", filename); //or any other extension
      document.body.appendChild(link);
      link.click();
      link.parentNode.removeChild(link);
    },
    async downloadFile(url, filename) {
      this.downloadInProgress = true;
      await this.axios({
        method: "get",
        url: process.env.VUE_APP_ROOT_DOWNLOAD + url,
        responseType: "arraybuffer",
        headers: {
          Authorization: ""
        },
        onDownloadProgress: progress => {
          this.progress = (progress.loaded / progress.total) * 100;
        }
      })
        .then(response => {
          this.forceFileDownload(response, filename);
        })
        .catch(e => bugsnagClient.notify(e));
      this.downloadInProgress = false;
    }
  }
};
</script>

<style scoped>
.full-height {
  height: 100%;
}
</style>
