<template>
  <div>
    <div v-for="(comment, index) in commentData" :key="index">
      <comment
        :user-image="comment.avatar_url ? comment.avatar_url : ''"
        :user-id="comment.user_id ? comment.user_id : ''"
        :user-name="comment.username ? comment.username : 'guest'"
        :text="comment.content.trim()"
        :comment-data="comment"
        class="col-12 "
        @reply="replyComment($event)"
        @edit="editComment($event)"
        @delete="deleteComment($event)"
      />
      <div v-if="comment.nestedComments">
        <div v-for="reply in comment.nestedComments" :key="reply.id">
          <comment
            :user-image="reply.avatar_url ? reply.avatar_url : ''"
            :user-id="reply.user_id ? reply.user_id : ''"
            :user-name="reply.username ? reply.username : 'anonymous'"
            :text="reply.content.trim()"
            :comment-data="reply"
            class="offset-1 col-11"
            @reply="replyComment($event)"
            @edit="editComment($event)"
            @delete="deleteComment($event)"
          />
        </div>
      </div>
    </div>
    <hr />
    <div v-if="access !== 'free'">
      <div class="media align-items-center">
        <img
          alt="Profile Picture"
          class="d-none d-sm-block avatar avatar-lg rounded-circle mr-4"
          :src="userAvatar"
          onerror="this.src='/dash/img/brand/symbol-black-center.svg'"
        />
        <div class="media-body">
          <editor
            ref="editor"
            :comment="newComment"
            :reply-to="newComment.replyTo"
          />
        </div>
      </div>
      <div class="text-right mt-2">
        <base-button type="secondary" @click="clearComment" :disabled="sending"
          >Cancel</base-button
        >
        <base-button @click="postComment()" :loading="sending"
          >Comment</base-button
        >
      </div>
    </div>
    <div v-else>
      <p class="text-center h3 mb-4">
        Only active subscribers can post comments. <br />
        Access this and other great features by upgrading today!
      </p>
      <div
        class="col-12 col-sm-10 offset-sm-1 col-md-8 offset-md-2 col-xl-6 offset-xl-3"
      >
        <upgrade />
      </div>
    </div>
  </div>
</template>

<script>
import swal from "sweetalert2";
import bugsnagClient from "@/lib/bugsnag";

import Comment from "./CommentItem";
import Editor from "./Editor";
import Upgrade from "./Upgrade";

export default {
  name: "Comments",
  components: {
    Upgrade,
    Editor,
    Comment
  },
  props: {
    lessonId: String,
    commentData: [Array, Object]
  },
  data() {
    return {
      newComment: {},
      sending: false
    };
  },
  computed: {
    userAvatar() {
      return this.$store.state.userInfo
        ? this.$store.state.userInfo.userAvatar
        : "";
    },
    userName() {
      return this.$store.state.userInfo ? this.$store.state.userInfo.name : "";
    },
    access() {
      return this.$store.state.userInfo
        ? this.$store.state.userInfo.access
        : "free";
    },
    premium() {
      return ["premium", "admin"].includes(this.access);
    },
    admin() {
      return this.$store.state.userInfo.access === "admin";
    },
    editor() {
      return this.$refs.editor;
    }
  },
  methods: {
    clearComment() {
      this.editor.clearContent();
      this.newComment = {};
    },
    replyComment(comment) {
      this.editor.setContent(`<p></p>`, 1);
      this.newComment = {
        replyTo: comment.username,
        replyToId: comment.reply_to_id ? comment.reply_to_id : comment.id,
        replyToId2: comment.reply_to_id ? comment.id : 0
      };
    },
    editComment(comment) {
      this.editor.setContent(comment.content);
      this.newComment = { commentId: comment.id };
    },
    deleteComment(comment) {
      swal.queue([
        {
          title: "Are you sure?",
          text: "You won't be able to revert this!",
          type: "warning",
          confirmButtonText: "Yes, delete it!",
          showLoaderOnConfirm: true,
          preConfirm: () => {
            return this.$http
              .delete(
                process.env.VUE_APP_ROOT_API + `/api/v1/lessons/comments`,
                {
                  data: {
                    lessonId: this.lessonId,
                    commentId: comment.id,
                    isAdmin: this.admin
                  }
                }
              )
              .then(() => {
                this.$emit("update");
                swal.insertQueueStep({
                  type: "success",
                  title: "Comment deleted!"
                });
              })
              .catch(e => {
                bugsnagClient.notify(e);
                swal.insertQueueStep({
                  type: "error",
                  title: "Unable to delete at this time"
                });
              });
          }
        }
      ]);
    },
    async postComment() {
      this.newComment = {
        ...this.newComment,
        ...{
          lessonId: this.lessonId,
          content: this.editor.getHTML(),
          isAdmin: this.admin
        }
      };
      //TODO Content validation
      if (
        this.newComment.content === "<p></p>" ||
        this.newComment.content === ""
      ) {
        this.$notify({
          type: "danger",
          message: "Sorry, cannot post an empty comment"
        });
        return;
      }

      this.sending = true;
      if (this.newComment.commentId) {
        await this.$http
          .patch(
            process.env.VUE_APP_ROOT_API + `/api/v1/lessons/comments`,
            this.newComment
          )
          .then(() => {
            this.$notify({ type: "success", message: "Posted a comment." });
            this.$emit("update");
            this.editor.clearContent();
            this.newComment = {};
          })
          .catch(e => {
            bugsnagClient.notify(e);
            this.$notify({
              type: "danger",
              message:
                "Sorry, we were unable to save your comment. Please check your connection and try again."
            });
          });
      } else {
        await this.$http
          .post(
            process.env.VUE_APP_ROOT_API + `/api/v1/lessons/comments`,
            this.newComment
          )
          .then(() => {
            this.$notify({ type: "success", message: "Posted a comment." });
            this.$emit("update");
            this.editor.clearContent();
            this.newComment = {};
          })
          .catch(e => {
            bugsnagClient.notify(e);
            this.$notify({
              type: "danger",
              message:
                "Sorry, we were unable to save your comment. Please check your connection and try again."
            });
          });
      }
      this.sending = false;
    }
  },
  mounted() {}
};
</script>

<style lang="scss" scoped>
@import "~sweetalert2/dist/sweetalert2.min.css";
</style>
