<template>
  <div v-if="level && progressTarget" class="row">
    <div class="d-none d-md-block col-xl-3 col-md-6">
      <stats-card
        title="7 day streak"
        type="gradient-red"
        :sub-title="thisWeek.toString()"
        icon="ni ni-paper-diploma"
      >
        <template slot="footer">
          <div v-if="changeWeek > 1000">
            <span>Great work! You're picking up pace!</span>
          </div>
          <div v-else-if="thisWeek === 0 && lastWeek === 0">
            <span>No recent history. Study some lessons!</span>
          </div>
          <div v-else>
            <span
              class="mr-2"
              :class="[changeWeek > 0 ? 'text-success' : 'text-danger']"
              ><i
                class="fa"
                :class="[changeWeek > 0 ? 'fa-arrow-up' : 'fa-arrow-down']"
              ></i>
              {{ changeWeek }}%</span
            >
            <span class="text-nowrap">Since last week</span>
          </div>
        </template>
      </stats-card>
    </div>
    <div class="d-none d-md-block col-xl-3 col-md-6">
      <stats-card
        title="30 day streak"
        type="gradient-orange"
        :sub-title="thisMonth.toString()"
        icon="ni ni-paper-diploma"
      >
        <template slot="footer">
          <div v-if="changeMonth > 1000">
            <span>Great work! You're picking up pace!</span>
          </div>
          <div v-else-if="thisMonth === 0 && lastMonth === 0">
            <span>No recent history. Study some lessons!</span>
          </div>
          <div v-else>
            <span
              class="mr-2"
              :class="[changeMonth > 0 ? 'text-success' : 'text-danger']"
              ><i
                class="fa"
                :class="[changeMonth > 0 ? 'fa-arrow-up' : 'fa-arrow-down']"
              ></i>
              {{ changeMonth }}%</span
            >
            <span class="text-nowrap">Since last month</span>
          </div>
        </template>
      </stats-card>
    </div>
    <div class="d-none d-md-block col-xl-3 col-md-6">
      <stats-card
        type="gradient-primary"
        title="progress"
        :sub-title="`${progressCurrent}/${progressTarget}`"
        icon="ni ni-atom"
      >
        <template slot="footer">
          <base-progress
            class="progress-xs mt-3 mb-0"
            type="success"
            :value="
              progressCurrent < progressTarget
                ? (progressCurrent / progressTarget) * 100
                : 100
            "
          ></base-progress>
        </template>
      </stats-card>
    </div>
    <div class="col-xl-3 col-md-6">
      <stats-card
        title="Current Level"
        :type="levelRaw.toLowerCase()"
        :sub-title="level"
        :icon="`ni ni-chart-bar-32`"
      >
        <template slot="footer">
          <div class="row">
            <div class="col-6 mt-2" :class="resize ? 'col-12' : ''">
              <!--            <div class="col-6 mt-2">-->
              <base-dropdown class="btn-block">
                <base-button
                  block
                  slot="title-container"
                  type="primary"
                  lass="dropdown-toggle"
                  size="sm"
                  outline
                  :disabled="!!changingLevel"
                  ><i class="ni ni-settings-gear-65"></i
                  ><span class="d-block d-md-block">Change</span></base-button
                >
                <li>
                  <a class="dropdown-item" @click="changeLevel('newbie')">
                    <i class="ni ni-hat-3 newbie"></i> Newbie
                  </a>
                </li>
                <li>
                  <a class="dropdown-item" @click="changeLevel('elementary')">
                    <i class="ni ni-hat-3 elementary"></i> Elementary
                  </a>
                </li>
                <li>
                  <a class="dropdown-item" @click="changeLevel('preInt')">
                    <i class="ni ni-hat-3 preint"></i> Pre Intermediate
                  </a>
                </li>
                <li>
                  <a class="dropdown-item" @click="changeLevel('intermediate')">
                    <i class="ni ni-hat-3 intermediate"></i> Intermediate
                  </a>
                </li>
                <li>
                  <a class="dropdown-item" @click="changeLevel('upperInt')">
                    <i class="ni ni-hat-3 upperint"></i> Upper Intermediate
                  </a>
                </li>
                <li>
                  <a class="dropdown-item" @click="changeLevel('advanced')">
                    <i class="ni ni-hat-3 advanced"></i> Advanced
                  </a>
                </li>
              </base-dropdown>
            </div>
            <div class="col-6 mt-2" :class="resize ? 'col-12' : ''">
              <base-button
                block
                tag="a"
                type="primary"
                size="sm"
                outline
                href="/placement"
                aria-pressed="true"
                ><i class="ni ni-paper-diploma"></i
                ><span class="d-block d-md-block">Placement</span></base-button
              >
            </div>
          </div>
        </template>
      </stats-card>
    </div>
  </div>
</template>

<script>
import StatsCard from "@/components/Cards/StatsCard";
import BaseProgress from "@/components/BaseProgress";
import BaseButton from "@/components/BaseButton";
import BaseDropdown from "@/components/BaseDropdown";

export default {
  name: "Stats",
  components: {
    StatsCard,
    BaseButton,
    BaseProgress,
    BaseDropdown
  },
  data() {
    return {
      changingLevel: false,
      windowSize: 1000
    };
  },
  computed: {
    thisWeek() {
      return this.$store.state.userStats.goals
        ? this.$store.state.userStats.goals.thisWeek
        : 0;
    },
    lastWeek() {
      return this.$store.state.userStats.goals
        ? this.$store.state.userStats.goals.lastWeek
        : 0;
    },
    thisMonth() {
      return this.$store.state.userStats.goals
        ? this.$store.state.userStats.goals.thisMonth
        : 0;
    },
    lastMonth() {
      return this.$store.state.userStats.goals
        ? this.$store.state.userStats.goals.lastMonth
        : 0;
    },
    changeWeek() {
      return this.$store.state.userStats.goals
        ? Math.round(
            (this.$store.state.userStats.goals.thisWeek /
              this.$store.state.userStats.goals.lastWeek) *
              100
          ) - 100
        : 0;
    },
    changeMonth() {
      return this.$store.state.userStats.goals
        ? Math.round(
            (this.$store.state.userStats.goals.thisMonth /
              this.$store.state.userStats.goals.lastMonth) *
              100
          ) - 100
        : 0;
    },
    progressCurrent() {
      return this.$store.state.userStats.progress
        ? this.$store.state.userStats.progress.current
        : 0;
    },
    progressTarget() {
      return this.$store.state.userStats.progress
        ? this.$store.state.userStats.progress.target
        : 100;
    },
    level() {
      const levelMap = {
        newbie: "Newbie",
        elementary: "Elementary",
        preInt: "Pre Intermediate",
        intermediate: "Intermediate",
        upperInt: "Upper Intermediate",
        advanced: "Advanced",
        media: "Media"
      };
      return this.$store.state.userInfo
        ? levelMap[this.$store.state.userInfo.level]
        : "Set it here!";
    },
    levelRaw() {
      return this.$store.state.userInfo
        ? this.$store.state.userInfo.level
        : "newbie";
    },
    resize() {
      return this.windowSize >= 1200 && this.windowSize < 1599;
    }
  },
  methods: {
    async changeLevel(level) {
      this.changingLevel = true;
      await this.$store.dispatch("changeLevel", level);
      this.changingLevel = false;
    }
  },
  mounted() {
    this.windowSize = window.innerWidth;
  }
};
</script>

