<template>
  <card>
    <!-- Card header -->
    <h3 slot="header" class="mb-0">New Dashboard Feedback Form</h3>

    <!-- Card body -->
    <div class="row">
      <div class="col-lg-8">
        <p class="mb-0">
          We appreciate any feedback you can provide in these early stages of
          the <strong>New ChinesePod Dashboard</strong>.
        </p>
      </div>
    </div>
    <hr />
    <div v-show="success">
      <base-alert type="success" dismissible>
        <span class="alert-inner--text"
          ><strong>Successfully sent!</strong> Thank you for your feedback! Feel
          free to leave more feedback as you use the new dashboard. We
          appreciate all the comments we can get.</span
        >
        <button
          type="button"
          class="close"
          data-dismiss="alert"
          aria-label="Close"
        >
          <span aria-hidden="true">&times;</span>
        </button>
      </base-alert>
      <router-link to="/home">
        <base-button type="primary">Back to Dashboard</base-button>
      </router-link>
    </div>
    <form v-show="!success" @submit.prevent="submitFeedback()">
      <base-input
        label="What do you think of the new website and the new HOME page with the new dashboard?"
      >
        <textarea
          class="form-control"
          id="feedbackTextarea"
          rows="5"
          v-model="feedback.feedback"
        ></textarea>
      </base-input>
      <base-input label="How likely would you be to switch to the new layout?">
        <button-radio-group
          :options="likelyToSwitchOptions"
          :button-classes="
            mobileDevice
              ? 'btn-secondary mx-1 my-2 btn-block'
              : 'btn-secondary mx-1'
          "
          v-model="feedback.likelyToSwitch"
        >
        </button-radio-group>
      </base-input>
      <base-input
        label="Do you have specific comments on the site? Any bugs encountered?"
      >
        <textarea
          class="form-control"
          id="bugTextarea"
          rows="5"
          v-model="feedback.comments"
        ></textarea>
      </base-input>
      <base-input label="Please tell us how you study lessons on ChinesePod:">
        <button-radio-group
          :options="methodsToStudyOptions"
          :button-classes="
            mobileDevice
              ? 'btn-secondary mx-1 my-2 btn-block'
              : 'btn-secondary mx-1'
          "
          v-model="feedback.howToStudy"
        >
        </button-radio-group>
      </base-input>
      <base-input
        label="What is the operating system of your primary mobile device:"
      >
        <button-radio-group
          :options="primaryMobileDeviceOptions"
          :button-classes="
            mobileDevice
              ? 'btn-secondary mx-1 my-2 btn-block'
              : 'btn-secondary mx-1'
          "
          v-model="feedback.primaryDevice"
        >
        </button-radio-group>
      </base-input>
      <transition name="fade">
        <base-input
          v-show="feedback.primaryDevice === 'other'"
          prepend-icon="fas fa-mobile-alt"
          placeholder="Your device"
          type="text"
          v-model="feedback.other"
        ></base-input>
      </transition>
      <div>
        <base-input
          label="Would you like to receive a response to your feedback form?"
        >
          <base-checkbox
            v-model="feedback.wantsResponse"
            @input="enableFeedback"
          >
            Yes, please!
          </base-checkbox>
        </base-input>
        <transition name="fade">
          <div v-show="feedback.wantsResponse" class="form-group row">
            <div class="col-12">
              <base-input
                prepend-icon="fas fa-user"
                placeholder="Your name"
                type="text"
                v-model="feedback.name"
              ></base-input>
            </div>
            <div class="col-12">
              <base-input
                prepend-icon="fas fa-envelope"
                placeholder="Email"
                type="email"
                v-model="feedback.email"
              ></base-input>
            </div>
          </div>
        </transition>
      </div>
      <div>
        <base-input
          label="Would you like to participate in upcoming beta tests of
          ChinesePod Apps?"
        >
          <base-checkbox v-model="feedback.wantsToParticipate">
            Keep me notified!
          </base-checkbox>
        </base-input>
      </div>
      <base-button type="success" :loading="loading" native-type="submit"
        >Send Feedback</base-button
      >
    </form>
  </card>
</template>
<script>
import { mapState } from "vuex";
import bugsnagClient from "@/lib/bugsnag";

import ButtonRadioGroup from "../../../components/ButtonRadioGroup";
export default {
  components: { ButtonRadioGroup },
  computed: {
    ...mapState({
      userInfo: "userInfo"
    }),
    mobileDevice() {
      return window.innerWidth < 768;
    }
  },
  data() {
    return {
      validated: false,
      success: false,
      loading: false,
      likelyToSwitchOptions: [
        { value: "very-likely", label: "Very Likely" },
        { value: "likely", label: "Likely" },
        { value: "maybe", label: "Maybe" },
        { value: "unlikely", label: "Unlikely" },
        { value: "very-unlikely", label: "Very Unlikely" }
      ],
      methodsToStudyOptions: [
        { value: "only-app", label: "Only Through the Mobile App" },
        { value: "mostly-app", label: "Predominantly Through the Mobile App" },
        { value: "mostly-web", label: "Predominantly Through the Website" },
        { value: "only-web", label: "Only Through the Website" }
      ],
      primaryMobileDeviceOptions: [
        { value: "android", label: "Android" },
        { value: "ios", label: "iPhone/iPad" },
        { value: "other", label: "Other" }
      ],
      feedback: {
        feedback: null,
        likelyToSwitch: "very-likely",
        comments: null,
        howToStudy: null,
        primaryDevice: null,
        other: null,
        wantsToParticipate: false,
        wantsResponse: false,
        name: null,
        email: null
      }
    };
  },
  methods: {
    async firstFormSubmit() {
      try {
        await this.$validator.validateAll();
      } finally {
        this.validated = true;
      }
    },
    async submitFeedback() {
      this.loading = true;
      await this.axios
        .post(
          process.env.VUE_APP_ROOT_API + "/api/v1/feedback/dashboard-feedback",
          this.feedback
        )
        .then(() => {
          this.loading = false;
          this.success = true;
          this.$notify({
            type: "info",
            message: `Thank you for your feedback. We appreciate your time and effort.`
          });
          this.feedback = {
            feedback: "",
            likelyToSwitch: "very-likely",
            comments: "",
            howToStudy: "",
            primaryDevice: "",
            other: "",
            wantsToParticipate: false,
            wantsResponse: false,
            name: "",
            email: ""
          };
        })
        .catch(e => {
          this.loading = false;
          bugsnagClient.metaData = { formData: this.feedback };
          bugsnagClient.notify(e);
          this.$notify({
            type: "danger",
            message: `Sorry, something went wrong.
            Please wait a moment and try again. If you're still experiencing this issue, please reach out to our team at support@chinesepod.com.`,
            timeout: 10000
          });
        });
    },
    getError(name) {
      return this.errors.first(name);
    },
    isValid(name) {
      return this.validated && !this.errors.has(name);
    },
    enableFeedback() {
      if (this.userInfo.name) {
        this.feedback.name = this.userInfo.name;
      }
      if (this.userInfo.email) {
        this.feedback.email = this.userInfo.email;
      }
    }
  }
};
</script>
