// import pinyinize from 'pinyinize'
import convertPinyinTones from "pinyin-tone-converter";

export default {
  // üāōēīūǖǖáóéíúǘǘǎǒěǐǔǚǚàòèìùǜǜ
  // ÜĀŌĒĪŪǕǕÁÓÉÍÚǗǗǍǑĚǏǓǙǙÀÒÈÌÙǛǛ
  regex: new RegExp(
    [
      "[^\\d\\w\\u00fc\\u0101\\u014d\\u0113\\u012b\\u016b\\u01d6\\u01d6\\u00e1",
      "\\u00f3\\u00e9\\u00ed\\u00fa\\u01d8\\u01d8\\u01ce\\u01d2\\u011b\\u01d0",
      "\\u01d4\\u01da\\u01da\\u00e0\\u00f2\\u00e8\\u00ec\\u00f9\\u01dc\\u01dc",
      "\\u00dc\\u0100\\u014c\\u0112\\u012a\\u016a\\u01d5\\u01d5\\u00c1\\u00d3",
      "\\u00c9\\u00cd\\u00da\\u01d7\\u01d7\\u01cd\\u01d1\\u011a\\u01cf\\u01d3",
      "\\u01d9\\u01d9\\u00c0\\u00d2\\u00c8\\u00cc\\u00d9\\u01db\\u01db]+"
    ],
    "g"
  ),

  removeNonChineseCharacters(sentence) {
    // TODO: check if this regex requires a polyfill
    const regex = /[^\u4E00-\u9FFF]+/gu;
    const result = sentence.toLowerCase().replace(regex, "");

    return result;
  },
  normalizeSentence(sentence) {
    return sentence
      .toLowerCase()
      .replace(this.regex, "")
      .replace(/[,.;:]+/g, "");
  },
  normalizeAnswer(answer) {
    // a-z, 0-9 and tone marks
    const answerSplitted = answer
      .toLowerCase()
      .replace(/[,.;:]+/g, " ")
      .replace(/(\d)(\w)/g, "$1 $2");

    return convertPinyinTones(answerSplitted).replace(/\s/g, "");
  }
};
